@charset "UTF-8";
.swiper-container {
  width: 100%;
  position: relative;
  overflow: hidden;
  padding: 16px; }

.swiper-wrapper {
  display: flex; }

.swiper-button-next {
  background-color: white;
  width: 42px;
  height: 42px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 100%;
  border: 1px solid #424c83;
  z-index: 4;
  right: 0px; }
  .swiper-button-next::before {
    content: "→";
    font-size: 28px;
    text-align: center;
    position: absolute;
    left: 16%;
    top: -2px;
    color: #424c83;
    z-index: 4; }

.swiper-button-prev {
  background-color: white;
  width: 42px;
  height: 42px;
  position: absolute;
  top: 50%;
  right: 0px;
  transform: translateY(-50%);
  border-radius: 100%;
  border: 1px solid #424c83;
  z-index: 4;
  left: 0px; }
  .swiper-button-prev::before {
    content: "←";
    font-size: 28px;
    text-align: center;
    position: absolute;
    left: 16%;
    top: -2px;
    color: #424c83;
    z-index: 4; }

body {
  margin: 0;
  padding: 0; }

.project {
  text-align: right;
  width: 300px;
  padding: 0 4px; }
  @media (min-width: 480px) {
    .project {
      width: 320px; } }
  @media (min-width: 768px) {
    .project {
      width: 425px; } }
  @media (min-width: 992px) {
    .project {
      width: 339px; } }
  @media (min-width: 1200px) {
    .project {
      width: 472px; } }
  .project-photo-container {
    width: 100%;
    position: relative; }
    .project-photo-container img {
      width: 290px; }
      @media (min-width: 480px) {
        .project-photo-container img {
          width: 305px; } }
      @media (min-width: 768px) {
        .project-photo-container img {
          width: 410px; } }
      @media (min-width: 992px) {
        .project-photo-container img {
          width: 325px; } }
      @media (min-width: 1200px) {
        .project-photo-container img {
          width: 450px; } }
  .project-title {
    padding-top: 32px;
    font-size: 24px;
    line-height: 29px; }
    @media (min-width: 992px) {
      .project-title {
        min-height: 89.6px; } }
    @media (min-width: 1200px) {
      .project-title {
        min-height: unset; } }
  .project-subtitle {
    font-size: 18px;
    padding-top: 16px; }
    @media (min-width: 1200px) {
      .project-subtitle {
        min-height: 68px; } }
  .project-buttons {
    display: flex;
    justify-content: flex-end; }
  .project .swiper-container {
    pointer-events: none; }
  .project small {
    display: block;
    text-align: right;
    height: 38px;
    align-self: flex-end; }
