@charset "UTF-8";
.swiper-container {
  width: 100%;
  position: relative;
  overflow: hidden;
  padding: 16px; }

.swiper-wrapper {
  display: flex; }

.swiper-button-next {
  background-color: white;
  width: 42px;
  height: 42px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 100%;
  border: 1px solid #424c83;
  z-index: 4;
  right: 0px; }
  .swiper-button-next::before {
    content: "→";
    font-size: 28px;
    text-align: center;
    position: absolute;
    left: 16%;
    top: -2px;
    color: #424c83;
    z-index: 4; }

.swiper-button-prev {
  background-color: white;
  width: 42px;
  height: 42px;
  position: absolute;
  top: 50%;
  right: 0px;
  transform: translateY(-50%);
  border-radius: 100%;
  border: 1px solid #424c83;
  z-index: 4;
  left: 0px; }
  .swiper-button-prev::before {
    content: "←";
    font-size: 28px;
    text-align: center;
    position: absolute;
    left: 16%;
    top: -2px;
    color: #424c83;
    z-index: 4; }

body {
  margin: 0;
  padding: 0; }

.projects {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  padding: 64px 8px 32px 8px; }
  @media (min-width: 480px) {
    .projects {
      padding: 64px 32px 32px 32px; } }
  @media (min-width: 992px) {
    .projects {
      padding: 0 32px; } }
  .projects > span {
    font-size: 34px;
    line-height: 41px;
    color: #242a49;
    margin-bottom: 32px; }
